.chat-container {
  width: 66%;
  height: 99.6vh;
  /* border: 1px solid black; */
  margin: 0px auto;
  box-sizing: border-box;
  overflow-y: hidden;
  overflow-x: hidden;
}
.chat-upper {
  width: 100%;
  height: 10%;
  /* border: 2px solid black; */
  font-family: "Lato", sans-serif;
  font-size: 22px;
  padding: 13px 23px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.chat-home {
  height: 100%;
  width: 100%;
  border-radius: 6px;
  border: none;
  outline: none;
  font-size: 15px;
  padding: 12px 23px;
